import React, { memo, useRef } from 'react';
import styles from './search_header.module.css'
import '@fortawesome/fontawesome-free/js/all.js'
const SearchHeader = memo(
  ({ onSearch }) => {

    const inputRef = useRef();


    const handleSearch = () => {
      const value = inputRef.current.value;
      onSearch(value);
    }
    const onClick_Search = (e) => {
      e.preventDefault();
      handleSearch();
    }
    const onKeyPress = (e) => {
      if (e.key === 'Enter') {
        handleSearch();
      }
    }

    return (
      <>
        <header className={styles.header}>
          <button className={styles.menu}>
            <i className="fas fa-bars"></i>
          </button>
          <a href="http://localhost:3000/" className={styles.logo}>
            <img src="/images/logo.png" alt="logo" />
            <h1 className={styles.title}>TUBE</h1>
          </a>

          <input ref={inputRef} className={styles.input} type="text" placeholder="Search.." onKeyPress={onKeyPress}></input>
          <button className={styles.btn} type="submit" onClick={onClick_Search}>
            <img className={styles.btnImg} src="/images/search.png" alt="search" />
          </button>
        </header>
      </>
    )
  })

export default SearchHeader;